'use client';

import { createContext, useContext, ReactNode, useState } from 'react';

// Define our own event types since we're getting them from the SDK
interface BaseEvent {
  event: string;
  data: any;
}

interface MessageEvent extends BaseEvent {
  event: 'messages/complete';
  data: {
    content: string;
    role?: string;
  };
}

interface ThoughtEvent extends BaseEvent {
  event: 'events';
  data: {
    type: 'thought';
    content: string;
  };
}

type StreamEvent = MessageEvent | ThoughtEvent | BaseEvent;

export interface Message {
  role: 'user' | 'assistant';
  content: string;
}

interface ChatContextType {
  messages: Message[];
  addMessage: (message: Message) => void;
  isLoading: boolean;
  setIsLoading: (loading: boolean) => void;
  events: StreamEvent[];
  addEvent: (event: StreamEvent) => void;
}

const ChatContext = createContext<ChatContextType | undefined>(undefined);

interface ChatProviderProps {
  children: ReactNode;
}

export function ChatProvider({ children }: ChatProviderProps) {
  const [messages, setMessages] = useState<Message[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [events, setEvents] = useState<StreamEvent[]>([]);

  const addMessage = (message: Message) => {
    setMessages((prev) => [...prev, message]);
  };

  const addEvent = (event: StreamEvent) => {
    setEvents((prev) => [...prev, event]);
    
    // If it's a message completion event, add it to messages
    if (event.event === 'messages/complete') {
      const content = typeof event.data === 'string' ? event.data : event.data.content;
      addMessage({
        role: event.data.role || 'assistant',
        content,
      });
    }
  };

  return (
    <ChatContext.Provider 
      value={{ 
        messages, 
        addMessage, 
        isLoading, 
        setIsLoading,
        events,
        addEvent 
      }}
    >
      {children}
    </ChatContext.Provider>
  );
}

export function useChat() {
  const context = useContext(ChatContext);
  if (!context) {
    throw new Error('useChat must be used within a ChatProvider');
  }
  return context;
}
